<template>
  <div class="row-right col-md-12" :id="data.key">
    <div class="form-group" >
      <div class="input-group" >
        <label class="label-head label-head-lg label-head-top  text-right" ><span :class="data.required?'require':''">{{data.label}}</span></label>
        <a v-if="contractData" v-for="item in contractData" :href="item.url" target="_blank">
          <div class="btn btn-white" style="margin-right:5px;margin-bottom: 5px;">{{item.name}}</div>
        </a>
        <div class="input-group-btn">
          <div v-if="loading=='loading'" class="btn btn-primary"  > ...</div>
          <div v-else  class="btn btn-primary"  @click="handleGetContract"> </div>
        </div>
      </div>

    </div>
  </div>
</template>
<style>

</style>
<script>
  import {mapGetters} from 'vuex';
  import * as api from '../api';
  import checkRule from '../mixins/checkRule'
  export default{
    mixins:[checkRule],
        data(){
            return{
              loading:'',
              contractData:''
            }
        },
      props: ['data'],
      created:function () {
          var _this=this;
        var href=tools.www_root()+'/getResourceById?resourceId='
        _this.getCractList(function (data) {
          var tempData=[]
          for(var k in data){
            var item=data[k];
            item.url=href+item.resourceId;
            tempData.push(item);
          }
          _this.contractData=tempData;
        })
      },
      computed:{
        ...mapGetters({
          'hiddenData': 'defaultHiddenData',
        }),
      },
      methods:{
        //
        getCractList:function (cb) {
          var _this=this;
          var options={
            url:'/contract/resource/queryContractByBpId',
            method:'get',
            data:{
              bpId:_this.hiddenData.bpId
            },
            beforeSend:function () {
              _this.loading='loading';
            },
            complete:function () {
              _this.loading=true;
            }
          };
          //
          api.getContract(options,function (data) {
            cb(data)
          })
        },
        //
        getData:function (cb) {
          var _this=this;
          var options={
            url:'/contract/resource/generate',
            data:{
              bpId:_this.hiddenData.bpId
            },
            beforeSend:function () {
              _this.loading='loading';
            },
            complete:function () {
              _this.loading=true;
            }
          };
          //
          api.getContract(options,function (data) {
            cb(data)
          })
        },
        //
        handleGetContract:function () {
          var _this=this;
          var href=tools.www_root()+'/getResourceById?resourceId='
          _this.getData(function (data) {
            var tempData=[]
            for(var k in data){
              var item=data[k];
              item.url=href+item.resourceId;
              tempData.push(item);
            }
            _this.contractData=tempData;
          });
        },
      }
    }
</script>



