<template>
  <div class="form-inline">
    <div class="form-group col-sm-12">
      <input type="text" class="hiddenInput" :name="data.key">
      <div class="input-group"  v-if="!data.readonly">
        <span class="input-group-addon "><span :class="data.required?'require':''">{{data.label}}</span></span>
        <!--<div class="form-control">
          <div class="file-caption-name" v-if="showFileName" :title="fileName">
            <i class="glyphicon glyphicon-file"></i>
            {{fileName}}
          </div>
        </div>-->
        <div class="input-group-btn">
          <!--<div class="btn btn-upload" v-if="state!='uploading'" @click="handleUpload">
            <i class="glyphicon glyphicon-upload"></i>
            <span></span>
          </div>-->
          <div :id="'pick-'+data.key" class="btn btn-upload" :class="data.key">
          </div>
        </div>
      </div>
      <div class="input-group" v-if="data.readonly">
        <div class="multi-file-border"><span :class="data.required?'require':''">{{data.label}}</span></div>
      </div>
      <div class="filelist">
        <div class="no-files" v-if="files.length==0"></div>
        <li v-if="files.length>0" v-for="(file,index) in files" class="col-sm-6">
          <div class="input-group" v-if="file">
            <div class="form-control">
              <div class="file-caption-name" :title="file.name||file.fileName">
                <i class="glyphicon glyphicon-file"></i>
                <span v-if="file.resourceId">
                    <a href="javascript:void(0)" @click="handleLook(file)">{{file.fileName}}</a></span>
                <span v-else>{{file.name}}</span>

              </div>
              <div class="fileNote">
                <!--loading-->
                <i v-if="file.state=='loading'" class="fa fa-spinner fa-spin"></i>
                <!--success-->
                <i v-else-if="file.state=='success'"
                   class="fa fa-check fileSuccess"></i>
                <!--error-->
                <i v-else-if="file.state=='error'" class="fa fa-close fileError"></i>
              </div>
            </div>
            <div class="input-group-btn" v-if="!data.readonly">
              <div class="btn btn-upload" @click="handleRemove(file,index)">
                <i class="glyphicon glyphicon-trash">
                </i>
                <span></span>
              </div>
            </div>
          </div>

        </li>
      </div>
    </div>
    <div class="form-error-tip" v-if="errorMsg[data.key]">{{errorMsg[data.key]}}</div>
  </div>
</template>
<style>

</style>
<script>
  import * as api from '../api';
  import checkRule from '../mixins/checkRule'
  export default{
    mixins:[checkRule],
    data(){
      return {
//        state: '',//
        files: [],//
        uploader: '',//
//        uploadState: {},//
        resourceId: []
      }
    },
    props: ['index', 'hiddenData'],
    created: function () {
      var attr=this.getDefaultValue(this.data.key);
      var arr=attr.split(',');
      for(var key in arr){
        this.resourceId.splice(key,1,arr[key]);
      }
      this.$store.dispatch('setInputData', {[this.data.key]: this.resourceId.join(',')})
      this.$watch('resourceId',this.handleWatch,{deep:true})
    },
    computed: {
      rules(){
        var accept = {};//
        var other = {};
        var ruleData = this.ruleData[this.data.key];
        for (var key in ruleData) {
          var item = ruleData[key];
          if (item.ruleType == 'file') {
            var detail = item.detail;
            accept.extensions = detail.extension;
            accept.mimeTypes = detail.type;
            other = detail;
            other.message = item.message
          }
        }
        return {
          accept: accept,
          other: other
        };
      }
    },
    mounted: function () {
      var _this = this;
      var BASE_URL = '/static/assets/plugins/webuploader';
      var pick = '#pick-' + this.data.key;
      //webuploader
      if (!this.uploader && !this.data.readonly) {
        var uploader = WebUploader.create({
          auto:true,
          swf: BASE_URL + '/Uploader.swf',
          pick: {
            id: pick,
            label: '<i class="glyphicon glyphicon-folder-open"></i><span></span>'
          },
          server: tools.www_root() + '/fileUpload',
          accept: _this.rules.accept,
          fileNumLimit: _this.rules.other.number,
          fileSingleSizeLimit: _this.rules.other.maxSize,
          clickArea:{
            width:'100%',
            height:'100%'
          }
        });
        _this.uploader = uploader;
        uploader.option('formData',{
          bpId:_this.hiddenData.bpId
        });
        uploader.on('uploadBeforeSend', function (obj, data, headers) {
          $.extend(headers, {
            Accept: "*/*"
          });
        });
        //
        uploader.on('fileQueued', function (file) {
          _this.addFile(file)
        });
        //
        uploader.on('uploadStart',function (file) {
          _this.changeFileState(file.id,'loading')
        });
        //
        uploader.on('uploadSuccess', function (file, res) {
          if (res.status == 1) {
            var data=res.data;
            _this.changeFileState(file.id,'success');
            _this.resourceId.splice(0,0,data.resourceId)
          } else {
            _this.changeFileState(file.id,'error');
          }
        });
        //
        uploader.on('uploadError', function (file) {
          _this.changeFileState(file.id,'error');
        });
        //validate
        uploader.on('error', function (type) {
          switch (type) {
            case 'Q_EXCEED_NUM_LIMIT':
              $.alert('' + _this.rules.other.number + '');
              break;
            case 'Q_TYPE_DENIED':
              $.alert(_this.rules.other.message);
              break;
            case 'F_EXCEED_SIZE':
              var size = WebUploader.formatSize(_this.rules.other.maxSize, ['B', 'KB', 'MB'])
              $.alert('' + size);
              break;
          }
        })
      }
      if (!this.files[0]) {
       var attr=this.getDefaultValue(this.data.key);
        if (attr != '') {
          this.getUploadFile(attr);
        }
      }
    },
    methods: {
      /*//
      handleUpload: function () {
        //
        var _this = this;
        var uploader = _this.uploader;
        if (_this.files) {
          $.each(_this.files, function (index, file) {
            if (file.id && !file.resourceId) {
              _this.uploadState[file.id] = 'loading';
              uploader.option('formData', {
                bpId: _this.hiddenData.bpId
              });
              uploader.upload(file);
            }
          });
        }
      },*/
      //
      handleRemove: function (file, index) {
        if (file.id) {
          this.uploader.removeFile(file);
          this.files.splice(index, 1)
        }
        if (file.resourceId) {
          this.files.splice(index, 1)
        }
        this.resourceId.splice(index, 1);
      },
      //
      changeFileState:function (id,state) {
        var _this=this;
        $.each(_this.files,function (key, item) {
          if(item.id==id){
            _this.$set(_this.files[key],'state',state);
//            _this.files[key].state=state;
            return false;
          }
        })
      },
      //
      addFile: function (file) {
        this.files.push(file)
      },
      //
      getUploadFile: function (value) {
        if (value) {
          this.$store.dispatch('setInputData', {[this.data.key]:value});
          var options = {
            url: '/getResources',
            method: 'get',
            data: {
              resourceIds: value
            }
          };
          var _this = this;
          api.getUploadFiles(options, function (data) {
            for (var key in data) {
              var item = data[key];
              _this.files.push(item);
            }
          })
        }
      },
      //
      handleLook:function (file) {
        var type=file.fileType;
        var href=tools.www_root()+'/getResourceById?resourceId='+file.resourceId;
        if(type.indexOf('image')>=0){
          tools.looksImgModal(href)
        }else{
          window.location.href=href;
        }
      },
      handleWatch(val){
        var valData={};
        valData[this.data.key]=val.join(',');
        this.checkRuleValue=Object.assign({},this.checkRuleValue,valData);
        this.$store.dispatch('setInputData', valData)
      }

    }

  }
</script>



