<template>
  <div class="col-md-12">
    <div class="form-group" >
      <div class="input-group inline-box">
        <label class="label-head label-head-lg label-head-top  text-right" ><span class="require">:</span></label>
        <div class="label-box">
          <div class="inline-block" v-for="(item,key) in data">
            <label class="radio-inline " >
              <input type="radio" name="operation" :value="item.operation" v-model="checkedValue">
              <span>{{item.operationName}}</span>
            </label>
          </div>
          <div class="form-error-tip"></div>
        </div>

      </div>

    </div>
    <div class="form-group">
      <div class="input-group inline-box">
        <label class="label-head label-head-lg label-head-top  text-right" style="padding-top:0;"><span class="require"></span></label>
        <div class="label-box" style="width: 500px">
          <select class="form-control" v-model="selectReason" v-if="reasons&&reasons.length>0" style="margin-bottom:5px">
            <option value=""></option>
            <option v-for="item in reasons" :value="item">{{item}}</option>
          </select>
          <textarea  name="reason" class="form-control" rows="3" v-model="textReason" placeholder="100"></textarea>
          <div class="form-error-tip"></div>
      </div>

      </div>
    </div>
  </div>

</template>
<style>

</style>
<script>
  import { mapGetters } from 'vuex';
    export default{
        data(){
            return{
              checkedValue:'',
              selectReason:'',
              textReason:''
            }
        },
      props:['data','reasons'],
      created:function () {
          for(var key in this.operationData){
            var v=this.operationData[key];
            if(v.operationType==1){
              this.checkedValue=key;
            }
          }
      },
      computed:{
        operationData(){
          var temp={};
          for(var key in this.data){
            var v=this.data[key];
            temp[v.operation]=v;
          }
          return temp
        }
      },
      watch:{
        checkedValue(val){
          this.$store.dispatch('setInputData', {'operation':val,'operationType':this.operationData[val].operationType})
        },
        selectReason(val){
          var inputReason=val?[val,this.textReason].join(':'):this.textReason;
          this.$store.dispatch('setInputData', {'reason': inputReason})
        },
        textReason(val){
          if(val.length>100){
            val=val.substr(0,100);
            this.textReason=val;
          }
          var inputReason=this.selectReason?[this.selectReason,val].join(':'):this.textReason;
          this.$store.dispatch('setInputData', {'reason': inputReason})
        }
      }
    }
</script>



